import React, { lazy, Suspense } from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import ContactInfo from './ContactInfo';

export const officeQuery = graphql`
    query OfficeQuery {
        site {
            siteMetadata {
                profile {
                    office {
                        openingHours {
                            day
                            hours
                        }
                    }
                }
            }
        }
        mobileImageOffice: file(relativePath: { eq: "office.jpg" }) {
            childImageSharp {
                fluid(maxWidth: 335, quality: 100) {
                    ...GatsbyImageSharpFluid_withWebp_noBase64
                }
            }
        }
        desktopImageOffice: file(relativePath: { eq: "office.jpg" }) {
            childImageSharp {
                fluid(maxWidth: 354, quality: 100) {
                    ...GatsbyImageSharpFluid_withWebp_noBase64
                }
            }
        }
    }
`;

const GoogleMapsLazy = lazy(() => import('./GoogleMap'));

const containerStyle = {
    width: '100%',
    height: '325px',
    borderRadius: '10px',
};

const Office = () => {
    const isSSR = typeof window === 'undefined';

    return (
        <StaticQuery
            query={officeQuery}
            render={({
                site: {
                    siteMetadata: {
                        profile: {
                            office: { openingHours },
                        },
                    },
                },
                mobileImageOffice,
                desktopImageOffice,
            }) => (
                <section id="office">
                    <div className="container">
                        <h2 className="section-title">Irodánk</h2>
                        {!isSSR && (
                            <Suspense fallback={<div />}>
                                <GoogleMapsLazy containerStyle={containerStyle} />
                            </Suspense>
                        )}
                        <div className="office-details row">
                            <div className="col-sm-12 col-md-6 col-lg-4 office-details__col">
                                {/* TODO */}
                                <Img
                                    className="office-img"
                                    fluid={[
                                        mobileImageOffice.childImageSharp.fluid,
                                        {
                                            ...desktopImageOffice.childImageSharp.fluid,
                                            media: `(min-width: 900px)`,
                                        },
                                    ]}
                                    alt=""
                                />
                            </div>
                            <div className="col-sm-12 col-md-6 col-lg-4 office-details__col">
                                <h3>Nyitvatartás</h3>
                                <div>
                                    {openingHours.map(({ day, hours }) => (
                                        <div key={day} className="opening-hours__row">
                                            <strong className="opening-hours__day">{day}:</strong>{' '}
                                            <span className="opening-hours__time">{hours}</span>
                                        </div>
                                    ))}
                                </div>
                            </div>
                            <div className="col-sm-12 col-md-6 col-lg-4 office-details__col" id="contact">
                                <h3>Elérhetőség</h3>
                                <ContactInfo oneLiner={true} />
                            </div>
                        </div>
                    </div>
                </section>
            )}
        />
    );
};

export default Office;
