import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

export const aboutQuery = graphql`
    query AboutQuery {
        site {
            siteMetadata {
                profile {
                    about
                }
            }
        }
        mobileImageTeam: file(relativePath: { eq: "team.jpg" }) {
            childImageSharp {
                fluid(maxWidth: 400, quality: 100) {
                    ...GatsbyImageSharpFluid_withWebp_noBase64
                }
            }
        }
        desktopImageTeam: file(relativePath: { eq: "team.jpg" }) {
            childImageSharp {
                fluid(maxWidth: 550, quality: 100) {
                    ...GatsbyImageSharpFluid_withWebp_noBase64
                }
            }
        }
    }
`;

const About = () => (
    <StaticQuery
        query={aboutQuery}
        render={({
            site: {
                siteMetadata: {
                    profile: { about },
                },
            },
            mobileImageTeam,
            desktopImageTeam,
        }) => (
            <section id="about" className="about">
                <div className="container">
                    <div className="row">
                        <div className="col-sm-12 col-md-6">
                            <h2 className="section-title">Rólunk</h2>
                            <div className="about-content">
                                <p className="description">{about}</p>
                                <a className="btn btn-primary hero-cta" href="#contact" role="button">
                                    Lépjen kapcsolatba
                                </a>
                            </div>
                        </div>
                        <div className="col-sm-12 col-md-6">
                            <div className="about-photo">
                                {/* TODO */}
                                <Img
                                    className="about-img"
                                    fluid={[
                                        mobileImageTeam.childImageSharp.fluid,
                                        {
                                            ...desktopImageTeam.childImageSharp.fluid,
                                            media: `(min-width: 900px)`,
                                        },
                                    ]}
                                    alt=""
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )}
    />
);

export default About;
