import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import { FaUser, FaQuoteRight } from 'react-icons/fa';

export const testimonialQuery = graphql`
    query TestimonialQuery {
        site {
            siteMetadata {
                testimonials {
                    name
                    text
                }
            }
        }
    }
`;

const Testimonials = () => {
    return (
        <StaticQuery
            query={testimonialQuery}
            render={({
                site: {
                    siteMetadata: { testimonials },
                },
            }) => (
                <section id="testimonial">
                    <div className="container">
                        <h2 className="section-title">Check out our customer reviews</h2>
                        <div className="testimonials">
                            {testimonials.map(({ name, text }) => (
                                <div className="testimonial" key={name}>
                                    <FaQuoteRight size="3em" className="testimonial__quote" />
                                    <div>
                                        <FaUser size="2em" className="testimonial__thumbnail" />
                                    </div>
                                    <div>
                                        <h3>{name}</h3>
                                        <p className="description">{text}</p>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </section>
            )}
        />
    );
};

export default Testimonials;
